<template>
    <div style="padding: 5px 20px;">
      <br>
      <el-form :inline="true" :model="listQuery" class="demo-form-inline">
        <el-form-item label="职业名称">
          <el-input v-model="listQuery.employName" placeholder="职业名称"></el-input>
        </el-form-item>
        <el-form-item>

          <el-button type="primary" @click="onSubmitSelect" icon="el-icon-search">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="success" @click="TJCollectionForm" icon="el-icon-edit">添加</el-button>
        </el-form-item>
      </el-form>
      <!-- 表格-->
      <el-table :data="list" element-loading-text="Loading" border fit highlight-current-row>
        <el-table-column align="center" label="ID" width="50" type="index">
        </el-table-column>
        <el-table-column label="职业名称" prop="employName">
        </el-table-column>
        <el-table-column label="专业" prop="professional">
        </el-table-column>
        <el-table-column label="学历" prop="educational">
        </el-table-column>
        <el-table-column label="薪资" prop="moneyType">
        </el-table-column>
        <el-table-column label="创建时间" prop="createTime">
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="修改密码" placement="top">
              <el-button type="primary" icon="el-icon-edit" circle @click="BJCollectionForm(scope.$index)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <el-button type="danger" icon="el-icon-delete" circle @click="deleteVisible(scope)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="listQuery.page"
        :page-sizes="[10]" :page-size="listQuery.limit" layout="total, sizes, prev, pager, next, jumper" :total="total"
        :background="true">
      </el-pagination>

      <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible">
        <el-form ref="collectionform" :rules="rules" :model="ruleForm" label-width="120px" label-position="left">
          <el-form-item label="职业名称" prop="employName" >
            <el-input placeholder="职业名称" v-model="ruleForm.employName"></el-input>
          </el-form-item>
          <el-form-item label="专业" prop="professional" >
            <el-input placeholder="专业" v-model="ruleForm.professional"></el-input>
          </el-form-item>
          <el-form-item label="学历" prop="educational">
            <el-radio-group v-model="ruleForm.educational">
              <el-radio label="专科" >专科</el-radio>
              <el-radio label="本科">本科</el-radio>
              <el-radio label="研究生">研究生</el-radio>
              <el-radio label="博士">博士</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="薪资" prop="educational">
            <el-radio-group v-model="ruleForm.moneyType">
              <el-radio label="5K以下" >5K以下</el-radio>
              <el-radio label="5k-10k">5k-10k</el-radio>
              <el-radio label="10K-20K">10K-20K</el-radio>
              <el-radio label="20K-30K">20K-30K</el-radio>
              <el-radio label="30k以上">30k以上</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" v-if="dialogStatus=='create'" @click="submitCollectionForm('collectionform')">添 加</el-button>
          <el-button type="warning" v-else @click="updateCollectionForm('collectionform')">修 改</el-button>
        </div>
      </el-dialog>

    </div>
  </template>

<script>
import { request } from '../../reqeust/index'
export default {
  name: 'employManager',
  data () {
    return {
      /* typecode:this.$route.query.typecode,
           typetitle:this.$route.query.typetitle */
      listLoading: true,
      list: null,
      total: 0,
      listQuery: {
        current: 1,
        size: 10
      },
      dialogFormVisible: false,
      dialogStatus: 'create',
      textMap: {
        update: '修改职业',
        create: '添加职业'
      },

      ruleForm: {
        id: '',
        employName: '',
        professional: '',
        educational: '',
        moneyType: ''
      },

      rules: {
        employName: [{
          required: true,
          message: '请输入职业名称',
          trigger: 'blur'
        }],
        professional: [{
          required: true,
          message: '请输入专业',
          trigger: 'blur'
        }],
        educational: [{
          required: true,
          message: '请输入学历',
          trigger: 'blur'
        }],
        moneyType: [{
          required: true,
          message: '请输入薪资',
          trigger: 'blur'
        }]
      }
    }
  },
  created () {
    this.axiosdata()
  },
  methods: {
    handleSizeChange (val) {
      this.listQuery.size = val
      this.axiosdata()
    },
    handleCurrentChange (val) {
      this.listQuery.current = val
      this.axiosdata()
    },
    onSubmitSelect () {
      this.axiosdata()
    },
    axiosdata () {
      this.listLoading = true
      request({
        method: 'post',
        url: '/employ/list',
        data: this.listQuery
      }).then((response) => {
        console.log(response)
        this.listLoading = false
        this.list = response.data.records
        this.total = response.data.total
      })
    },

    /* 添加表单 */
    TJCollectionForm () {
      /* 表单重置 */
      Object.assign(this.$data.ruleForm, this.$options.data().ruleForm)

      this.dialogStatus = 'create'
      this.dialogFormVisible = true
    },
    /* 编辑表单 */
    BJCollectionForm ($index) {
      // 显示修改对话框
      this.ruleForm = JSON.parse(JSON.stringify(this.list[$index]))
      // 以下效果和上面的不一样
      // this.ruleForm=this.list[$index]

      this.dialogStatus = 'update'
      this.dialogFormVisible = true
    },

    /* 添加按钮 */
    submitCollectionForm (formname) {
      this.$refs[formname].validate((valid) => {
        if (valid) {
          request({
            method: 'post',
            url: '/employ/add',
            data: this.ruleForm
          }).then((response) => {
            console.log(response)
            if (response.code === 0) {
              this.axiosdata()
              this.dialogFormVisible = false
              this.$message.success('添加数据成功！')
            } else {
              this.$message.error('添加数据失败！')
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    /* 修改按钮 */
    updateCollectionForm (formname) {
      this.$refs[formname].validate((valid) => {
        if (valid) {
          request({
            method: 'post',
            url: '/employ/update',
            data: this.ruleForm
          }).then((response) => {
            if (response.code === 0) {
              this.axiosdata()
              this.dialogFormVisible = false
              this.$message.success('修改数据成功！')
            } else {
              this.$message.error('修改数据失败！')
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    /* 删除按钮 */
    deleteVisible (scope) {
      this.$confirm('确定要删除【' + scope.row.employName + '】吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        request({
          method: 'post',
          url: '/employ/del/' + scope.row.id,
          data: scope.row
        }).then((response) => {
          if (response.code === 0) {
            this.axiosdata()
            this.dialogFormVisible = false
            this.$message.error('删除完毕')
          } else {
            this.$message.error('删除数据失败！')
          }
        })
      })
    }
  }
}
</script>

  <style>
  </style>



